.text-body-primary {
  font-size: 2.7rem;
  font-weight: $medium;
}

.text-body-second {
  font-size: 2.5rem;
}

.text-body-third {
  font-size: 1.4rem;
}

[class^="text-title-"] {
  font-weight: $extrabold;
}

.text-title-primary {
  font-size: 2.5rem;
  
  @include media-query(tablet) {
    font-size: 3rem;
  }
  @include media-query(desktop) {
    font-size: 4rem;
  }

  &--yellow {
    color: $color-gold;
  }

  &--uppercase {
    text-transform: uppercase;
  }
}

.text-title-secondary {
  font-size: 3rem;
}

.text-title-third {
  font-size: 2rem;
}

.text-subtitle-primary {
  font-size: 2rem;
  font-weight: $regular;
}

.text-paragraph {
  font-size: 1.7rem;
  font-weight: $medium;

  &--light {
    font-weight: $light;
  }

  &--regular{
    font-weight: $regular;
  }

  &--semibold {
    font-weight: $bold;
  }

  &--bold {
    font-weight: $bold;
  }

  &--yellow {
    color: $color-gold;
  }
}

.text-green{
  color: $color-green;
}

.text-blue{
  // color: $color-bright-blue;
}

.text-yellow{
  color: $color-gold;
}

.text-white{
  color: $color-white;
}
.font{
 
  &-bold{
    font-weight: 900;
  }

  &-semi-bold{
    font-weight: 700;
  }

  &-medium{
    font-weight: 500;
  }

  &-light{
    font-weight: 200;
  }
  
}

.description{
  color:$color-dark-gray;
}

/* ==================== HEADINGS =================== */

.heading {
  &--1 {
    font-family: 'Bogart';
    font-weight: $semibold;
    line-height: 1.125;
    font-size: 30px;

    @include media-query(mobilex) {
      font-size: 40px;
    }

    @include media-query(mobilexl) {
      font-size: 48.8px;
    }

    @include media-query(mobile) {
      font-size: 57.76px;
    }

    @include media-query(tablet) {
      font-size: 66.64px;
    }

    @include media-query(desktop) {
      font-size: 75.52px;
    }

    @include media-query(desktopL) {
      font-size: 84.4px;
    }

    @include media-query(desktopLS) {
      font-size: 93.28px;
    }

    @include media-query(desktopXL) {
      font-size: 102.16px;
    }

    @include media-query(desktopXXL) {
      font-size: 120px;
    }
  }

  &--2 {
    font-family: 'Bogart';
    font-weight: $semibold;
    line-height: 1.125;
    font-size: 20px;

    @include media-query(mobilex) {
      font-size: 25px;
    }
  
    @include media-query(mobilexl) {
      font-size: 30px;
    }
  
    @include media-query(mobile) {
      font-size: 40px;
    }
  
    @include media-query(tablet) {
      font-size: 45px;
    }
  
    @include media-query(desktop) {
      font-size: 50px;
    }
  
    @include media-query(desktopL) {
      font-size: 55px;
    }
  
    @include media-query(desktopLS) {
      font-size: 60px;
    }
  }

  &--3 {
    font-family: 'Bogart';
    font-weight: $semibold;
    line-height: 1.125;
    font-size: 20px;
    
    @include media-query(mobilex) {
      font-size: 23px;
    }
    
    @include media-query(mobilexl) {
      font-size: 25px;
    }
    
    @include media-query(mobile) {
      font-size: 30px;
    }
    
    @include media-query(tablet) {
      font-size: 40px;
    }
    
    @include media-query(desktop) {
      font-size: 45px;
    }
    
    @include media-query(desktopL) {
      font-size: 50px;
    }
    
    @include media-query(desktopLS) {
      font-size: 55px;
    }
    
    @include media-query(desktopXL) {
      font-size: 60px;
    }
    
    @include media-query(desktopXXL) {
      font-size: 80px;
    }
  }

  &--4 {
    font-family: 'Bogart';
    font-weight: $semibold;
    line-height: 1.125;
    font-size: 20px;
    
    @include media-query(mobilexl) {
      font-size: 25px;
    }
    
    @include media-query(mobile) {
      font-size: 30px;
    }
    
    @include media-query(tablet) {
      font-size: 35px;
    }
    
    @include media-query(desktop) {
      font-size: 40px;
    }
    
    @include media-query(desktopL) {
      font-size: 45px;
    }
    
    @include media-query(desktopLS) {
      font-size: 50px;
    }
    
    @include media-query(desktopXL) {
      font-size: 55px;
    }
    
    @include media-query(desktopXXL) {
      font-size: 60px;
    }
  }

  &--5 {
    font-family: 'Bogart';
    font-weight: $semibold;
    line-height: 1.125;
    font-size: 17px;
    
    @include media-query(mobilexl) {
      font-size: 20px;
    }
    
    @include media-query(mobile) {
      font-size: 25px;
    }
    
    @include media-query(tablet) {
      font-size: 30px;
    }
    
    @include media-query(desktop) {
      font-size: 35px;
    }
    
    @include media-query(desktopL) {
      font-size: 40px;
    }
    
    @include media-query(desktopLS) {
      font-size: 45px;
    }
    
    @include media-query(desktopXL) {
      font-size: 50px;
    }
    
    @include media-query(desktopXXL) {
      font-size: 55px;
    }
  }
}